import GameConfig from "./GameConfig";
import Stage = Laya.Stage;
import GameLayer from "./game/GameLayer";
import Facade from "./module/Facade";
import MainUiPanelMediator from "./module/MainUiPanelMediator";
import { ToggleState } from "./module/ToggleState";
import TopUIPanelMediator from "./module/TopUIPanelMediator";
import BottomUiPanelMediator from "./module/BottomUiPanelMediator";
class Main {
	constructor() {
		//根据IDE设置初始化引擎		
		if (window["Laya3D"]) Laya3D.init(GameConfig.width, GameConfig.height);
		else Laya.init(GameConfig.width, GameConfig.height, Laya["WebGL"]);
		Laya["Physics"] && Laya["Physics"].enable();
		Laya["DebugPanel"] && Laya["DebugPanel"].enable();
		

		if (Laya.Browser.onPC) {
			Laya.stage.scaleMode = Stage.SCALE_SHOWALL;
			Laya.stage.alignH = Stage.ALIGN_CENTER;
        	Laya.stage.alignV = Stage.ALIGN_MIDDLE;
			Laya.stage.screenMode = Stage.SCREEN_NONE;
	    } else {
			Laya.stage.scaleMode = Stage.SCALE_FIXED_WIDTH;
			Laya.stage.alignH = Stage.ALIGN_LEFT;
			Laya.stage.alignV = Stage.ALIGN_TOP;
			Laya.stage.screenMode = Stage.SCREEN_VERTICAL;
		} 

		//兼容微信不支持加载scene后缀场景
		Laya.URL.exportSceneToJson = GameConfig.exportSceneToJson;

		//打开调试面板（通过IDE设置调试模式，或者url地址增加debug=true参数，均可打开调试面板）
		if (GameConfig.debug || Laya.Utils.getQueryString("debug") == "true") Laya.enableDebugPanel();
		if (GameConfig.physicsDebug && Laya["PhysicsDebugDraw"]) Laya["PhysicsDebugDraw"].enable();
		if (GameConfig.stat) Laya.Stat.show();	
		Laya.alertGlobalError(true);

		//激活资源版本控制，version.json由IDE发布功能自动生成，如果没有也不影响后续流程
		Laya.ResourceVersion.enable("version.json", Laya.Handler.create(this, this.onVersionLoaded), Laya.ResourceVersion.FILENAME_VERSION);
	}

	onVersionLoaded(): void {
		//激活大小图映射，加载小图的时候，如果发现小图在大图合集里面，则优先加载大图合集，而不是小图
		Laya.AtlasInfoManager.enable("fileconfig.json", Laya.Handler.create(this, this.onConfigLoaded));
	}

	onConfigLoaded(): void {
		//加载IDE指定的场景
		GameConfig.startScene && Laya.Scene.open(GameConfig.startScene);
		GameLayer.instance;
		Laya.stage.addChild(fgui.GRoot.inst.displayObject);


		Facade.toggle(TopUIPanelMediator,ToggleState.SHOW);
		Facade.toggle(BottomUiPanelMediator,ToggleState.SHOW);
		Facade.toggle(MainUiPanelMediator,ToggleState.SHOW);
	}

}
//激活启动类
new Main();
